import React, { useEffect, useState }  from 'react'
import "./MainNavbar2.css"
import { Link, useLocation } from 'react-router-dom';
import { navItems } from "../../assets/NavItems";
import MenuButton from "../menubutton/MenuButton";
import DropDownMenu from './dropDownMenus/Dropdown';
import DropDownTietoja from './dropDownMenus/DropdownTietoja';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

export default function MainNavbar2() {
const [dropdownT, setDropdownT] = useState(false);
const [dropdown, setDropdown] = useState(false);
const [navOpen, setNavOpen] = useState(false);
const location = useLocation(); 
const activeItem = navItems.find((item) => item.path === location.pathname);

useEffect(() => {
  setNavOpen(false); 
}, [location]);

return (
  <nav>
    <div className='mainnavoverlay2'/>
      <div className="navbar-links2">
        <img src={process.env.PUBLIC_URL + '/assets/images/SL_HEALTH_LOGO_TXT_SANNA_SLHEALTH_WHITE.png'} alt="Sl Health logo" className="minimenu-logoWhite"/>
        <ul className={navOpen ? "nav-items2 active2" : "nav-items2"}>
          <img src={process.env.PUBLIC_URL + '/assets/images/logo.png'} alt="Sl Health logo" className="logo-imagemain"/>
          <div className="mainNav-options">
            {navItems.map((item) => {
              if (item.title === 'Palvelut ja hinnasto') {
                return (
                  <li
                    key={item.id}
                    className={`${item.cName} ${item === activeItem ? 'activeItem' : ''}`}
                    id={item.itemId}
                    onClick={() => setDropdown(!dropdown)}
                    onMouseEnter={() => setDropdown(true)}
                    onMouseLeave={() => setDropdown(false)}
                  >
                    <Link id="mainNavA" to={item.path}>
                      {item.title}
                      <KeyboardArrowDownIcon
                        sx={{ fontSize: 30 }}
                        onClick={() => setDropdown(!dropdown)}
                      />
                    </Link>
                    {dropdown && <DropDownMenu />}
                  </li>
                );
              } else if (item.title === 'SL Health') {
                return (
                  <li
                    key={item.id}
                    className={`${item.cName} ${item === activeItem ? 'activeItem' : ''}`}
                    id={item.itemId}
                    onClick={() => setDropdownT(!dropdownT)}
                    onMouseEnter={() => setDropdownT(true)}
                    onMouseLeave={() => setDropdownT(false)}
                  >
                    <Link id="mainNavA" to={item.path}>
                      {item.title}
                      <KeyboardArrowDownIcon
                        sx={{ fontSize: 30 }}
                        onClick={() => setDropdown(!dropdownT)}
                      />
                    </Link>
                    {dropdownT && <DropDownTietoja />}
                  </li>
                );
              }
              return (
                <li
                  key={item.id}
                  className={`${item.cName} ${item === activeItem ? 'activeItem' : ''}`}
                  id={item.itemId}
                  >
                  <Link id="mainNavA" className={item.title} to={item.path}>
                    {item.title}
                  </Link>
                </li>
              );
            })}
          </div>
        </ul>
          <img src={process.env.PUBLIC_URL + '/assets/images/logo.png'} alt="Sl Health logo" className="minmenu-logo2" />
          <div className="SMenuBtn2" style={{ zIndex: 1001 }} onClick={() => setNavOpen(!navOpen)}>
        <MenuButton />
      </div>
    </div>
  </nav>
);
}

